<template>
	<section class="projects">
    <!-- Section header -->
    <div class="projects-header section-padding">
      <h2>Projects</h2>
      <!-- Progress control -->
      <div class="progress-control">
        <div class="progress-indicator">
          <span v-for="i in projects.length" :key="i" :class="{ current: i === index + 1 }" @click="scrollTo(i - 1)" />
        </div>
        <div class="progress-buttons">
          <span class="prev" @click="scrollTo(index - 1)">&#10094;</span>
          <span class="next" @click="scrollTo(index + 1)">&#10095;</span>
        </div>
      </div>
    </div>
    <!-- Horizontal scrolling list -->
    <div class="projects-scroller" ref="scroller">
      <!-- Projects container -->
      <div class="projects-container" ref="container">
        <!-- Project card -->
        <div v-for="project in projects" :key="project.title" class="project">
          <div class="thumbnail" :style="getBackgroundStyle(project.thumbnail)" />
          <div class="details" :class="{ 'has-link': project.url }">
            <!-- Project title -->
            <h3 class="title">{{ project.title }}</h3>
            <!-- Project description -->
            <div class="description">{{ project.description }}</div>
            <!-- Tags / relevance -->
            <div class="tags" v-if="project.tags">
              <span class="tag" v-for="tag in project.tags" :key="tag">{{ tag }}</span>
            </div>
            <!-- Call to action -->
            <a class="btn btn-visit" v-if="project.url" :href="project.url" rel="noopener noreferrer" target="_blank"
              @click="$gtag.event('Project', { value: project.title })">
              View <img src="@/assets/new_tab_gray.svg" alt="Opens in new tab" class="new-tab-icon" />
            </a>
          </div>
        </div>
      </div>
    </div>
	</section>
</template>

<script>
/**
 * title, thumbnail, link, description
 */
export default {
  props: {
    projects: Array
  },
  data() {
    return {
      progress: 0,
      index: 0
    }
  },
  mounted() {
    // Set CSS variable of the number of projects in the scroller
    document.documentElement.style.setProperty('--nr-projects', this.projects.length)
    // Attach scroll listener
    this.$refs.scroller.addEventListener('scroll', this.onScroll)
  },
  methods: {
    getBackgroundStyle(image) {
      return 'background-image: url("' + image + '");'
    },
    onScroll(e) {
      // Calculate the index of the project card that is in view
      this.progress = e.target.scrollLeft / (this.$refs.container.clientWidth - e.target.clientWidth)
      this.progress = Math.min(Math.max(.01, this.progress), 1)
      this.index = Math.ceil(this.progress / (1. / this.projects.length)) - 1
    },
    scrollTo(newIndex) {
      // Cycle indices
      newIndex = newIndex === -1 ? newIndex + this.projects.length : newIndex % this.projects.length
      // Get container padding
      const containerPadding = parseFloat(window.getComputedStyle(this.$refs.container, null).getPropertyValue('padding-left'))
      // Calculate scroll position of card at the provided index
      const newX = newIndex * (1. / this.projects.length) * (this.$refs.container.clientWidth - 2 * containerPadding)
      // Scroll!
      this.$refs.scroller.scrollTo({
        left: newX,
        behavior: 'smooth'
      })
    }
  }
}
</script>

<style lang="sass" scoped>
@use 'src/style/vars'
@use 'sass:math'

/*
 * 1. Used for the responsive design of the project cards
 */
$vw-card: 84vw         // ss0 value
$vw-padding: 4vw       // ss0 value
$vw-between-cards: 4vw // ss0 value

.projects
  .projects-header
    h2
      margin-bottom: 8px

    .progress-control
      display: flex
      justify-content: space-between
      align-items: center

      .progress-indicator
        padding-left: 2px

        span
          display: inline-block
          width: 8px
          height: 8px
          margin-right: 8px
          border-radius: vars.$border-radius
          background-color: vars.$element-background-color
          cursor: pointer

          &.current
            background-color: vars.$brand-color

      .progress-buttons
        span
          display: inline-block
          padding: 8px 13px  // 34x34
          font-size: 16px
          background-color: vars.$element-background-color
          border-radius: 50%
          text-align: center
          cursor: pointer
          user-select: none

          &.prev
            margin-right: 16px

  .projects-scroller
    overflow-x: scroll

    .projects-container
      padding-top: 5vh     // padding for the cards' shadow
      padding-bottom: 8vh  // padding for the cards' shadow
      padding-left: $vw-padding  /* 1 */
      padding-right: $vw-padding  /* 1 */
      width: calc((var(--nr-projects) * (#{$vw-card} + 2 * #{$vw-between-cards})) + 2 * #{$vw-padding})  /* 1 */

      .project
        vertical-align: top
        width: $vw-card  /* 1 */
        margin: 0 $vw-between-cards  /* 1 */

        border-radius: 6px
        box-shadow: 0 2vh 8vh -2vh #999
        overflow: hidden  // hide the thumbnail's shadow on the outside
        display: inline-grid

        .thumbnail
          height: $vw-card / 2  // 2:1 ratio
          overflow: hidden
          // border shadow, vignette
          box-shadow: 0 0 4px -1px #999, 0 0 64px -32px #00000033 inset
          // center the background image inside the div and cover the complete div
          background-size: cover
          background-repeat: no-repeat
          background-position: 50% 50%

        $detail-padding: 16px
        $btn-height: 32px
        .details
          position: relative
          padding: $detail-padding

          &.has-link
            padding-bottom: $detail-padding + $btn-height + 16px

          .title
            font-size: 1em
            margin-top: 0
            margin-bottom: 8px

          .description
            font-size: .9em

          .tags
            display: flex
            flex-wrap: wrap
            margin-top: 16px

            .tag
              margin: 0 4px 4px 0
              padding: 2px 6px
              background-color: vars.$element-background-color
              border-radius: vars.$border-radius
              font-size: .8em

          .btn-visit
            position: absolute
            left: $detail-padding
            right: $detail-padding
            bottom: $detail-padding
            margin: 0 auto

@media (hover: hover)
  .projects .projects-header .progress-control
    .progress-indicator span
      &:hover,
      &:focus
        background-color: vars.$element-background-color-dark
  
    .progress-buttons span
      &:hover,
      &:focus
        background-color: vars.$element-background-color-dark

@media (min-width: vars.$screen-size-2)
  /* 1 */
  $vw-card: 80vw
  $vw-between-cards: 3vw
  $vw-padding: 8vw

  .projects
    .projects-scroller
      .projects-container
        padding-left: $vw-padding  /* 1 */
        padding-right: $vw-padding  /* 1 */
        width: calc((var(--nr-projects) * (#{$vw-card} + #{2 * $vw-between-cards})) + 2 * #{$vw-padding})  /* 1 */

        .project
          width: $vw-card  /* 1 */
          margin: 0 $vw-between-cards  /* 1 */

          $detail-padding: 32px
          $btn-height: 32px
          .thumbnail
            height: $vw-card / 2  // 2:1 ratio
  
          .details
            padding: $detail-padding
            
            &.has-link
              padding-bottom: $detail-padding + $btn-height + 16px

            .title
              font-size: 1.2em
              margin-bottom: 24px

            .btn-visit
              left: $detail-padding
              right: $detail-padding
              bottom: $detail-padding

@media (min-width: vars.$screen-size-3)
  $vw-card: 70vw  /* 1 */
  $vw-between-cards: 3vw  /* 1 */
  $vw-padding: 12vw  /* 1 */

  .projects
    .projects-scroller
      .projects-container
        padding-left: $vw-padding  /* 1 */
        padding-right: $vw-padding  /* 1 */
        width: calc((var(--nr-projects) * (#{$vw-card} + #{2 * $vw-between-cards})) + 2 * #{$vw-padding})  /* 1 */
        display: flex  // cards have equal height

        .project
          width: $vw-card  /* 1 */
          margin: 0 $vw-between-cards  /* 1 */
          grid-template-columns: 1.2fr [thumbnail] 0.8fr [details]
          grid-template-rows: 1fr
          .thumbnail
            height: initial

          .details
            .title
              font-size: 1.4em
              margin-bottom: 32px

            .description
              font-size: 1em

            .tags
              margin-top: 24px

              .tag
                margin: 0 8px 8px 0
                font-size: .9em

@media (min-width: vars.$screen-size-4)
  $vw-card: 1100px  /* 1 */
  $vw-between-cards: 32px  /* 1 */
  $vw-padding: calc((100vw - #{$vw-card} - 2 * #{$vw-between-cards}) / 2)  /* 1 */

  .projects
    .projects-scroller
      .projects-container
        padding-left: $vw-padding  /* 1 */
        padding-right: $vw-padding  /* 1 */
        width: calc((var(--nr-projects) * (#{$vw-card} + 2 * #{$vw-between-cards})) + 2 * #{$vw-padding})  /* 1 */

        .project
          width: $vw-card  /* 1 */
          max-width: 1200px
          margin: 0 $vw-between-cards  /* 1 */

          .details
            .tags
              .tag
                margin: 0 8px 8px 0
</style>