<template>
  <nav class="menu" :style="menuStyle">
    <a class="btn menu-item" v-for="item in items" :key="item.title" @click="scrollTo(item.url)">
      {{ item.title }}
    </a>
  </nav>
</template>

<script lang="ts">
import smoothscroll from 'smoothscroll-polyfill'
smoothscroll.polyfill()

export default {
  props: {
    items: Array,
    skewAngle: {
      type: Number,
      default: -6
    }
  },
  methods: {
    scrollTo(query) {
      const el = document.querySelector(query)
      if (el) el.scrollIntoView({ behavior: 'smooth' })
    }
  },
  computed: {
    menuStyle() {
      return `
        padding-top: ${this.tangent * 20}vw;
        padding-bottom: ${this.tangent * 40}vw;
        transform: skewY(${this.skewAngle}deg);
      `
    },
    tangent() {
      return Math.abs(Math.tan(this.skewAngle * Math.PI/180))
    }
  }
}
</script>

<style lang="sass" scoped>
@use 'src/style/vars'

.menu
  display: flex
  flex-wrap: wrap
  flex-direction: column
  align-items: stretch
  justify-content: space-between
  margin: 0 auto

  .menu-item
    cursor: pointer
    flex-grow: 1
    margin-bottom: 16px

@media (min-width: vars.$screen-size-1)
  .menu
    max-width: vars.$max-el-width-ss-1
</style>