<template>
  <Modal ref="modal" :title="title">
    <h3>Average grade: {{ grade | round }}</h3>
    <div v-for="year in curriculum" :key="year.title">
      <h2>{{ year.title }}</h2>
      <div class="course" v-for="course in year.courses" :key="course.title">
        <div class="table-row">
          <span class="title">{{ course.title }}</span>
          <span class="ec">{{ course.credits }} EC</span>
          <span class="grade"><b>{{ course.grade }}</b> / 10</span>
        </div>
        <!-- <summary>{{ course.description }}</summary> -->
      </div>
    </div>
  </Modal>
</template>

<script>
import Modal from './Modal.vue'

export default {
  name: 'CurriculumModal',
  components: { Modal },
  data() {
    return {
      curriculum: [],
      title: 'Curriculum'
    }
  },
  computed: {
    grade() {
      let sum = 0
      let ecs = 0
      for (const year of this.curriculum) {
        for (const course of year.courses) {
          sum += course.grade * course.credits
          ecs += course.credits
        }
      }
      return sum / ecs
    }
  },
  methods: {
    show(title, curriculum) {
      this.title = title
      this.curriculum = curriculum
      this.$refs.modal.open()
    }
  },
  filters: {
    round(value) {
      if (!value) return ''
      return value.toFixed(1)
    }
  }
}
</script>

<style lang="sass" scoped>
@use 'src/style/vars'

.course
  &:last-child
    .table-row
      border: none
  .table-row
    display: flex
    flex-direction: row
    flex-wrap: nowrap
    padding-bottom: 4px
    margin-bottom: 8px
    border-bottom: 1px solid #eee

  .title
    flex-grow: 1
    margin: 0
  .ec, .grade, .summary
    font-size: .9rem
  .ec, .grade
    flex-grow: 0
    text-align: right
    white-space: nowrap
    min-width: 50px
    margin-left: 8px
    // margin: 0 4px 4px 0
    // padding: 2px 6px
    // background-color: vars.$element-background-color
    // border-radius: vars.$border-radius
  .summary
    display: block
</style>