<template>
<transition name="modal">
  <div v-show="show" class="modal-bkg" @click="close">
    <div class="modal" @click.stop="">
      <header v-if="showHeader">
        <h3>{{ title }}</h3>
      </header>
      <main>
        <slot></slot>
      </main>
      <footer v-if="showFooter">
        <a class="btn" href="#" @click.prevent="close">Close</a>
      </footer>
    </div>
  </div>
</transition>
</template>

<script>
export default {
  name: 'Modal',
  props: {
    title: String,
    showHeader: {
      type: Boolean,
      default: true
    },
    showFooter: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      show: false
    }
  },
  methods: {
    open() {
      this.show = true
    },
    close() {
      this.show = false
    }
  }
}
</script>

<style lang="sass" scoped>
$animation-flow: .3s ease

.modal-bkg
  display: flex
  align-items: center
  justify-content: center
  position: fixed
  z-index: 1000
  top: 0
  left: 0
  width: 100vw
  height: 100vh
  background-color: rgba(0, 0, 0, .5)
  transition: opacity $animation-flow

.modal
  display: flex
  flex-direction: column
  background-color: #fff
  box-shadow: 0 .5rem 3rem rgba(0, 0, 0, .66)
  transition: all $animation-flow

  header, footer
    flex-grow: 0
    text-align: center

  header
    border-bottom: 1px solid #ddd

    h3
      margin: 0
  
  main
    flex-grow: 1
    overflow-y: auto

  footer
    border-top: 1px solid #ddd
    .btn
      display: block
  
/*
 * The following styles are auto-applied to elements with
 * transition="modal" when their visibility is toggled
 * by Vue.js.
 *
 * You can easily play with the modal transition by editing
 * these styles.
 */

.modal-enter
  opacity: 0

.modal-leave-active
  opacity: 0

.modal-enter .modal,
.modal-leave-active .modal
  -webkit-transform: scale(1.1)
  transform: scale(1.1)

// RESPONSIVE

/* DEFAULT RESPONSIVE CSS */
@media  screen and (min-width: 0)
  $horizontal-padding: 24px
  .modal
    position: fixed
    left: 0
    right: 0
    top: 0
    bottom: 0

    header
      padding: 13px $horizontal-padding  
    main
      padding: 0 $horizontal-padding 32px
    footer
      padding: 16px $horizontal-padding


/* RESPONSIVE CSS AFTER BREAK 1 */
@media screen and (min-width: 400px)
  $horizontal-padding: 32px
  .modal
    position: static
    width: 90vw
    max-height: 90vh
    border-radius: .5rem
    header
      padding: 13px $horizontal-padding
    main
      padding: 0 $horizontal-padding 32px
    footer
      padding: 12px $horizontal-padding 12px
    .btn
      margin-left: auto
      margin-right: auto
      max-width: 300px


/* RESPONSIVE CSS AFTER BREAK 2 */
@media screen and (min-width: 700px)
  .modal
    width: 600px
    max-height: min(80vh, 800px)
</style>