<template>
  <section class="experience">
    <h2>{{ experience.name }}</h2>
    <!-- Experiences of same category (e.g. work or education) -->
    <div v-for="entry in experience.entries" :key="entry.organization.name" class="entry" :data-scroll-index="entry.organization.name">
      <!-- Positions -->
      <div class="positions">
        <div v-for="(pos, i) in entry.positions" :key="pos.title" :class="{ position: i !== 0 }">
          <!-- Current position -->
          <span v-if="i === 0" class="heading">
            <!-- Position title -->
            <span class="title">{{ pos.title }}</span>
            <!-- Organization name -->
            <span class="organization" :style="organizationStyle(entry.organization)">
              {{ entry.organization.name }}
            </span>
            <!-- Organization logo -->
            <div class="logo-container">
              <img v-if="entry.organization.logo" :src="entry.organization.logo" :alt="entry.organization.name" class="logo">
            </div>
          </span>
          <!-- Prior positions -->
          <span v-if="i !== 0" class="title">{{ pos.title }}</span>
          <!-- General info -->
          <span class="period">{{ pos.from }} - {{ pos.to }}</span>
          <span class="location">{{ pos.location }}</span>
          <span class="description">{{ pos.description }}</span>
          <!-- View curriculum -->
          <a class="link" href="#" v-if="!!pos.curriculum"
            @click.prevent="$emit('show-curr', {
              title: entry.organization.name, curriculum: pos.curriculum
            }); $gtag.event('Curriculum', { value: pos.title })">
            View curriculum
          </a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Experience',
  props: {
    experience: Object
  },
  methods: {
    organizationStyle(org) {
      return org.color ? 'color:' + org.color + ';' : ''
    }
  }
}
</script>

<style lang="sass">
@use 'src/style/vars'

.experience 
  .entry 
    margin-bottom: 28px
    display: flex

    .positions 
      width: 100%
      margin-top: 4px

      .position 
        padding-top: 12px
        margin-top: 0
        padding-left: 12px
        margin-left: 12px
        border-left: 1px solid #cfcfcf

        /* First prior position */
        &:nth-child(2) 
          padding-top: 0
          margin-top: 14px
      
      a
        font-size: .9em
        margin-top: 8px
        display: inline-block
        text-decoration-color: vars.$brand-color

      .heading 
        width: 100%
        display: flex
        flex-direction: column
        justify-content: space-between
        .organization
          margin: 6px 0
        .title, .organization 
          flex-grow: 1
          // flex-basis: 0
          font-weight: bold
          font-size: 1.1em

      .period, .location, .description
        margin-top: 4px
        display: block

      .location, .description
        margin-top: 6px

      .period, .location
        font-size: .9em

      .description
        font-size: 1rem
        color: vars.$text-vl-color
        white-space: pre-line
        line-height: 1.4

    .logo-container 
      display: none

@media (min-width: vars.$screen-size-2) 
  .experience 
    .entry 
      .positions 
        .heading 
          flex-direction: row
          align-items: center
          .organization
            margin-left: 16px
            text-align: right

        .position 
          padding-left: 20px
          margin-left: 20px

      .logo-container 
        // display: block
        padding-left: 16px
        width: 48px
        max-height: 28px
        flex-shrink: 0

        .logo 
          max-width: 32px
          max-height: 28px
</style>