<template>
  <header>
    <h1 class="name" :style="nameStyle">{{ name }}<span class="stylish-dot">.</span></h1>

    <Menu id="menu" :items="menu" :skewAngle="skewAngle" />

    <div class="flex">
      <!-- About me -->
      <div>
        <Introduction :intro="introduction"/>
        <!-- Socials -->
        <Socials :items="socials" />
      </div>

      <!-- Latest tweet  -->
      <!-- <div class="flex-tweet">
        <h3 class="border-bottom">Latest Tweet</h3>
        <div class="content-blocked">
          <div id="latest-tweet" class="darker">
            <a class="twitter-timeline" data-width="360" data-height="200" data-dnt="true" data-theme="dark"
              data-chrome="noheader nofooter noborders transparent" data-tweet-limit="1"
              :href="twitter">Latest Tweet</a>
          </div>
        </div>
      </div> -->
    </div>
  </header>
</template>

<script>
import Introduction from './Introduction.vue'
import Menu from './Menu.vue'
import Socials from './Socials.vue'

export default {
  name: 'header-intro',
  components: { Introduction, Menu, Socials },
  props: {
    name: String,
    menu: Array,
    introduction: Object,
    socials: Array,
    twitterId: String,
    skewAngle: {
      type: Number,
      default: -5
    }
  },
  computed: {
    twitter() {
      return `https://twitter.com/${this.twitterId}?ref_src=twsrc%5Etfw`;
    },
    nameStyle() {
      return `
        transform: skewY(${this.skewAngle}deg);
      `
    }
  }
}
</script>

<style lang="sass" scoped>
@use 'src/style/vars'

header 
  padding-top: 0

  .name 
    margin: 32px 0

  #menu 
    margin-left: -8px
    margin-right: -8px

  .flex 
    display: flex
    flex-direction: column
    margin-bottom: 32px

  .content-blocked 
    position: relative
    padding-bottom: 40px

    &::before 
      position: absolute
      z-index: -1
      top: 0
      content: "Blocked by your browser\'s content blocker."

  #latest-tweet 
    margin: 0 -10px

@media (min-width: vars.$screen-size-1)
  header
    #menu 
      margin: 0 auto
    .flex-tweet
      .border-bottom
        border-bottom: none

@media (min-width: vars.$screen-size-2) 
  header 
    font-size: 1.1em
    .name 
      display: none
      font-size: 4em
    #menu 
      display: none
    .flex
      margin-top: 32px

@media (min-width: vars.$screen-size-4) 
  header 
    .flex 
      flex-direction: row
      .flex-tweet 
        min-width: 40%
        max-width: 360px
        margin-top: 32px
        margin-left: 32px
        .border-bottom
          border-bottom: 1px solid #ddd
        h3
          margin-top: 0
</style>