<template>
  <div id="app">
    <!-- Scroll label -->
    <ScrollLabel class="scroll-label" />

    <!-- Portrait picture -->
    <div class="portrait-container dark" data-scroll-index="About me" :style="portraitStyle">
      <img class="portrait" :src="publicPath + data.introduction.portrait" />

      <!-- Bigger screen details -->
      <div class="portrait-details">
        <h1 class="hero-name" :style="nameStyle">{{ data.name }}<span class="stylish-dot">.</span></h1>
        <Menu id="portrait-menu" :items="data.menu" :skewAngle="skewAngle" />
      </div>
    </div>

    <!-- Header -->
    <Skewed class="skew-header darker" :angle="skewAngle" :style="headerStyle">
      <Header class="section-padding" :name="data.name" :menu="data.menu" :socials="data.socials"
        :introduction="data.introduction" :twitterId="data.twitterId" :skewAngle="skewAngle" />
    </Skewed>

    <!-- CV Content -->
    <Skewed class="skew-content light" :angle="skewAngle">
      <Content :data="data" :skewAngle="skewAngle" @show-curr="showCurriculum($event)" />
    </Skewed>

    <!-- Footer -->
    <Skewed class="skew-footer dark" :angle="skewAngle">
      <Footer class="section-padding" :name="data.name" :socials="data.socials" />
    </Skewed>

    <!-- Cookie consent -->
    <CookieConsent class="section-padding" />

    <!-- Modal -->
    <CurriculumModal ref="modal" />
  </div>
</template>

<script>
import CurriculumModal from './components/CurriculumModal.vue'
import CookieConsent from './components/CookieConsent.vue'
import ScrollLabel from './components/ScrollLabel.vue'
import Header from './components/Header.vue'
import Content from './components/Content.vue'
import Skewed from './components/Skewed.vue'
import Footer from './components/Footer.vue'
import Menu from './components/Menu.vue'

// CV data to dynamically load in
// console.log(process.env.BASE_URL + 'cvdata.js')
// const data = require(process.env.BASE_URL + 'cvdata.js')
import data from '@/cvdata.js'

export default {
  name: 'app',
  components: { Header, ScrollLabel, Content, Skewed, Footer, Menu, CurriculumModal, CookieConsent },
  data() {
    return {
      data,
      publicPath: process.env.BASE_URL,
      skewAngle: -5
    }
  },
  computed: {
    portraitStyle() {
      return `
        padding-top: ${this.tangent * 50}vw;
        padding-bottom: ${this.tangent * 20}vw
      `
    },
    headerStyle() {
      return `
        margin-top: -${this.tangent * 55}vw
      `
    },
    nameStyle() {
      return `
        transform: skewY(${this.skewAngle}deg)
      `
    },
    tangent() {
      return Math.abs(Math.tan(this.skewAngle * Math.PI/180))
    }
  },
  methods: {
    showCurriculum(event) {
      this.$refs.modal.show(event.title, event.curriculum)
    }
  }
}
</script>

<style lang="sass">
@use 'src/style/vars'
@use 'src/style/basic'

.scroll-label
  margin: 0 16px
  padding: 8px 16px
  border-radius: 6px
  box-shadow: 0 0 64px 0 #333333bb
  background-color: #333333aa
  color: vars.$text-color-light
  font-weight: bold

// HEADER

.portrait-container
  .portrait
    display: block
    margin: 0 auto
    width: 100%
    max-width: vars.$max-el-width-ss-1

  .portrait-details
    display: none
    margin-left: 16px

.skew-header .name, .hero-name 
  margin: 0 auto
  font-size: 14vw
  text-align: center
  font-family: 'Sacramento', 'Alata', 'Avenir', Helvetica, Arial, sans-serif
  white-space: nowrap
  color: vars.$text-color-light

.hero-name 
  font-size: 8vw

.skew-header 
  position: relative
  z-index: 100  // header
  box-shadow: vars.$shadow

// CONTENT

.skew-content 
  padding-bottom: 32px

// FOOTER

.skew-footer 
  footer 
    padding-top: 16px
    padding-bottom: 0

// RESPONSIVE

@media (min-width: vars.$screen-size-1) 
  .portrait-container 
    .portrait 
      max-width: 50%

@media (min-width: vars.$screen-size-2) 
  .portrait-container 
    display: flex
    align-items: center
    padding-left: 64px
    padding-right: 64px

    .portrait 
      margin: 0

    .portrait-details 
      display: block
      flex-grow: 1

@media (min-width: vars.$screen-size-3) 
  .portrait-container
    .portrait 
      max-width: vars.$screen-size-3 / 2

@media (min-width: 2000px) 
  .portrait-container 
    padding-left: 15%
    padding-right: 15%
</style>
