<template>
  <div class="stats-consent">
    <!-- Cookie consent management in footer -->
    <Skewed class="stats-footer">
      This website uses cookies for statistical purposes (visitor count, etc). You can (revoke) consent at any time:
      <p>
        {{ consent ? '🥦 ' : '🍪 ' }}
        <a href="#" @click.prevent="c = !consent; setConsent(c); $gtag.event('Consent', { event_category: 'Footer', value: c })">
          {{ consent ? 'No thank you.' : 'That\'s okay.' }}
        </a>
      </p>
    </Skewed>

    <!-- Cookie consent banner -->
    <div v-if="!consentGiven" class="stats-banner">
      Welcome!
      <p>
        --------------------------<br/>
        This website uses cookies for statistical purposes (visitor count, etc).<br/>
        --------------------------
      </p>
      <p>🍪 <a href="#" @click.prevent="setConsent(true); $gtag.event('Consent', { event_category: 'Banner', value: true })">That's okay.</a></p>
      <p>🥦 <a href="#" @click.prevent="setConsent(false); $gtag.event('Consent', { event_category: 'Banner', value: false })">No thank you.</a></p>
    </div>
  </div>
</template>

<script>
import Skewed from './Skewed.vue'

const KEY = '_cc'

export default {
  name: 'CookieConsent',
  components: { Skewed },
  data() {
    return {
      consentGiven: false,
      consent: false
    }
  },
  mounted() {
    // Get stored consent values
    this.consentGiven = !!localStorage.getItem(KEY)
    this.consent = localStorage.getItem(KEY) === 'true'
    // Initial load attempt
    if (this.consent) this.enableGtag()

    // Time page load
    this.$gtag.time({
      name: 'load',
      value: Math.round(performance.now())
    })
  },
  methods: {
    setConsent(accept) {
      localStorage.setItem(KEY, accept ? 'true' : 'false')
      this.consentGiven = true
      this.consent = !!accept

      if (this.consent) this.enableGtag()
      else this.disableGtag()
    },
    enableGtag() {
      this.$gtag.optIn()
      // console.log('GTag opted in.')
    },
    disableGtag() {
      this.$gtag.optOut()
      this.clearCookies()
      // console.log('GTag opted out.')
    },
    clearCookies() {
      const cookies = document.cookie.split(';')
      for (let i = 0; i < cookies.length; i++) {
          const cookie = cookies[i]
          const eqPos = cookie.indexOf('=')
          const name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie
          document.cookie = name + '=;expires=Thu, 01 Jan 1970 00:00:00 GMT'
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@use 'src/style/vars'

.stats-consent
  .stats-footer, & a
    color: #ccc
  
  .stats-banner
    position: fixed
    z-index: 2000
    right: 0
    bottom: 64px
    max-width: 320px
    padding: 16px
    background-color: #333
    border-radius: 2px
    opacity: .9

    &, a
      font-family: monospace
      color: #fff

@media (hover: hover)
  .stats-consent
    .stats-banner
      a:hover
        color: vars.$brand-color
        font-weight: bold
</style>